<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('CreateGCV') }}
                            </h5>
                            <button class="close" v-on:click="$parent.isGCVShow = false">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-md-12">
                                    <dynamic-form lang="en" ref="refAddress" :buttons="false" :schema="FormSchema">

                                        <template v-slot:tgslot-SetGCVYear="{ data }">
                                            <div class="input-group mb-3">
                                                <MonthPickerInput :no-default="true" v-model="DateofMeasurement"
                                                    class="form-control"></MonthPickerInput>
                                                <div class="input-group-append">
                                                    <span class="input-group-text"><em
                                                            class="fa fa-calendar"></em></span>
                                                </div>
                                            </div>
                                            <!-- <div class="form-group mb-0">
                                                </div> -->
                                            <!-- <v-date-picker  @input="isGCVExist(DateofMeasurement)" v-bind:name="DateofMeasurement" mode="month"  class="timedate  input-group" v-model="DateofMeasurement">
                                                        <template v-slot="{ inputValue, togglePopover }">
                                                            <input placeholder="YYYY/MM"  @click="togglePopover()" v-bind:class="{'form-control' : true}" :value="formatDate(inputValue)"  />
                                                            <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                        </template>
</v-date-picker> -->
                                            <!-- <MonthPickerInput  @input="isGCVExist(DateofMeasurement)"  :no-default="true" v-model="DateofMeasurement"></MonthPickerInput> -->




                                        </template>

                                    </dynamic-form>
                                </div>

                                <div class="col-md-12">
                                    <div class="row">

                                        <div class="col-sm-12">


                                            <div class="row flex-row-reverse mt-2">
                                                <div class="col-lg-6 text-right mb-2">

                                                    <a @click="onSubmit" class="btn btn-success mr-2 " title=""
                                                        id="addAddress" data-toggle-tooltip="tooltip"
                                                        data-original-title="Save">
                                                        <i class="fa fa-save mr-2"></i>
                                                        <span id="btnAddressText"> {{ $t("Save") }}</span></a>

                                                    <a @click="$parent.isGCVShow = false" class="btn btn-danger mr-2"
                                                        title="" id="CancelAddress" data-toggle-tooltip="tooltip"
                                                        data-original-title="Cancel"><i class="fa fa-times mr-2"></i>{{
                                                            $t("Cancel")
                                                        }}</a>
                                                </div>
                                            </div>

                                        </div>


                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-2"
                                    style="max-height: 300px; overflow-y: auto; width: 100%;"> <!-- Add this wrapper -->
                                    <div class="listing">
                                        <div class="table-responsive">

                                            <table class="table table-bordered nowrap mt-0 mb-0"
                                                aria-describedby="layoutfilter">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th class="text-center" scope="col"><span>{{ $t("yearmonth")
                                                        }}</span></th>
                                                        <th class="text-center" scope="col"><span>{{ $t("setGcvvalue")
                                                        }}</span></th>

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(item, index) in stationGcvList" :key="index">
                                                        <td class="text-center">{{
                                                            formatListDate(item.GCV_DATE_OF_MEASUREMENT) }}</td>
                                                        <td class="text-center">{{ item.GCV_VALUE }}</td>
                                                    </tr>
                                                    <tr v-if="stationGcvList.length == 0" style="cursor:initial;">
                                                        <td colspan="4">
                                                            <div
                                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                                {{ $t("NoRecordfound") }}
                                                            </div>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div> <!-- Close the wrapper -->
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>
<style>
.month-picker-input {
    border: 0px !important;
}
</style>
<script>
import DataService from '../../services/DataService';
import MapComponent from '../../components/Modals/MapComponent.vue';
import { MonthPickerInput } from 'vue-month-picker'

export default {
    name: "Billing",
    props: {
        stationData: {
            type: Array
        }
    },
    components: {
        MapComponent, MonthPickerInput
    },
    data() {
        return {
            config: {
                isShowDrawButton: true,
                isShowSearchBox: true
            },
            meterServiceOptions: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "SlotField",
                            label: this.$t('GCVYear'),
                            name: "SetGCVYear",
                            value: "",
                            placeholder: "MM-YYYY",
                            validationRules: {
                                required: true,
                                max: 250,
                            },

                        },
                        {
                            astype: "NumericField",
                            label: this.$t('setGcvvalue'),
                            name: "SetGCVValue",
                            value: 0,
                            placeholder: "",
                            validationRules: {
                                max: 9,
                                required: false,
                            },

                        },

                    ],
                },

            ],
            latLngValues: '',
            isStationId: 0,
            isUpdateForm: false,
            GCVDate: null,
            DateofMeasurement: null,
            stationGcvList: []
            // ]
        };
    },

    async created() {

        var vm = this;
        vm.getGcvList(vm.stationData.STATION_ID)
    },

    methods: {
        togglePopover() { },
        formatListDate(dateStr) {
            debugger
            // Ensure dateStr is a string
            if (typeof dateStr !== 'string') {
                dateStr = String(dateStr); // Convert to string if it's not
            }
            // Ensure the string is exactly 6 characters long
            if (dateStr.length === 6) {
                const year = dateStr.substring(0, 4); // Get the first 4 characters for the year
                const monthNumber = dateStr.substring(4, 6); // Get the last 2 characters for the month
                const monthNames = [
                    "January", "February", "March", "April", "May", "June",
                    "July", "August", "September", "October", "November", "December"
                ];

                const monthIndex = parseInt(monthNumber, 10) - 1; // Convert to zero-based index
                const monthName = monthNames[monthIndex]; // Get the month name

                return `${year}/${monthName}`;
            }
            return ''; // Return empty if the format is incorrect
        },
        formatDate(date) {
            debugger
            if (!date) return '';
            const d = new Date(date);
            const year = d.getFullYear();
            const month = (d.getMonth() + 1).toString().padStart(2, '0'); // Months are zero-based
            return `${year}/${month}`;
        },
        // isGCVExist(itm)
        // {
        //     debugger
        //     // const date = new Date(itm);
        //     // const year = date.getFullYear();
        //     // const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-indexed
        //     const month = String(itm.monthIndex).padStart(2, '0');
        //     let selectedMonth= `${itm.year}${month}`;
        //     console.log('selectedMonth',selectedMonth)

        //     let isExist = this.stationGcvList.filter(x=>x.GCV_DATE_OF_MEASUREMENT==selectedMonth)
        //     if(isExist.length>0)
        //     {
        //         this.ShowAlert(this.$t("The GCV month already exists. Are you sure you want to update the value for this month?"), "warning", this.$t('Alert'));
        //     }
        // },
        formattedDate(itm) {
            // const date = new Date(itm);
            // const year = date.getFullYear();
            // const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-indexed
            const month = String(itm.monthIndex).padStart(2, '0');
            return `${itm.year}${month}`;
        },
        onSubmit() {
            var vm = this;
            vm.$refs.refAddress.validateForm.validate().then(async valid => {
                if (valid) {
                    let monthData = vm.formattedDate(vm.DateofMeasurement)
                    let param = `station_id=${vm.stationData.STATION_ID}&gcv_month=${monthData}&gcv_value=${vm.FormSchema[0].Data[1].value == '' ? 0.00 : vm.FormSchema[0].Data[1].value}&statusId=1001`
                    const obj = JSON.stringify(param);
                    var requestPayload = { objData: obj };
                    DataService.SaveGCVValue(requestPayload).then(response => {
                        if (response.data != null) {
                            if (response.data.code == 1) {
                                vm.ShowAlert(vm.$t("Gcv_Added_Success"), "success", vm.$t('Alert'));
                                vm.getGcvList(vm.stationData.STATION_ID);
                            }
                            if (response.data.code == 2) {
                                vm.ShowAlert(vm.$t("Gcv_Updated_Success"), "success", vm.$t('Alert'));
                                vm.getGcvList(vm.stationData.STATION_ID);
                            }
                        }
                        vm.FormSchema[0].Data[1].value = '';
                        vm.DateofMeasurement = ''
                    })
                }
            })
        },
        getGcvList(stationId) {

            var vm = this;
            let param = `station_id=${stationId}`
            DataService.GetGCVByStationId(param).then(response => {

                if (JSON.parse(response.data.result).DATA != null) {
                    vm.stationGcvList = JSON.parse(response.data.result).DATA
                }
                else {
                    vm.stationGcvList = []
                }
            })
        },
    },


}
</script>

<style>
.month-picker-input-container.form-control {
    padding: 0 !important;
}

.month-picker-input {
    min-height: calc(1.5em + .75rem + 3px) !important;
    height: auto !important;
    padding: .375rem .75rem !important;
    background-color: #e9ecef;
    border-radius: 0px !important;
    width: 100%;
}

.month-picker__container {
    z-index: 99;
    top: 34px !important;
    border-radius: 0px;
    border: 1px solid #c6cace;
}

.month-picker__year button {
    line-height: 16px;
    height: 2.0rem;
}

.month-picker__year {
    padding: 0.1rem;
}

.month-picker__year p {
    font-size: 16px !important;
    margin: 0.7rem 0 !important;
}

.month-picker__month {
    padding: 0.5rem 0.25rem !important;
}

.month-picker__month:hover::after {
    box-shadow: 0 0 10px rgb(164 223 248 / 88%);
}
</style>