<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('createstation') }}
                            </h5>
                            <button class="close" v-on:click=" clearForm()">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-md-6">
                                    <dynamic-form lang="en" ref="refAddress" :buttons="false"
                                        :schema="FormSchema"></dynamic-form>
                                </div>

                                <div class="col-md-6">
                                    <div class="row">

                                        <div class="col-sm-12">

                                            <MapComponent class="row" ref="mapComponnet" mapType="MapBox"
                                                mapContainer="addAddress" :config="config" mapHeight="355px"
                                                @GetAddress="GetAddress" :latLng="latLngValues">
                                            </MapComponent>


                                            <div class="row flex-row-reverse mt-2">
                                                <div class="col-lg-6 text-right">
                                                    <a href="javascript:;" class="btn btn-danger mr-2" title=""
                                                        id="CancelAddress" @click="clearForm()"
                                                        data-toggle-tooltip="tooltip" data-original-title="Cancel"><i
                                                            class="fa fa-times mr-2"></i>{{ $t("Cancel")
                                                            }}</a>

                                                    <a v-if="isUpdateForm == true" href="javascript:;" @click="onSubmit"
                                                        class="btn btn-success  mr-2" title="" id="addAddress"
                                                        data-toggle-tooltip="tooltip" data-original-title="Save">
                                                        <i class="fa fa-refresh"></i>
                                                        <span id="btnAddressText"> {{ $t("Update") }}</span></a>

                                                    <a v-else href="javascript:;" @click="onSubmit"
                                                        class="btn btn-success mr-2 " title="" id="addAddress"
                                                        data-toggle-tooltip="tooltip" data-original-title="Save">
                                                        <i class="fa fa-save mr-2"></i>
                                                        <span id="btnAddressText"> {{ $t("Save") }}</span></a>


                                                </div>
                                            </div>

                                        </div>


                                    </div>
                                </div>
                            </div>


                            <div class="row col-lg-6">
                                <div class="py-2">
                                    <span class="mandatory">{{ $t("Mandatory") }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>
<script>
import DataService from '../../services/DataService';
import MapComponent from '../../components/Modals/MapComponent.vue';

export default {
    name: "Billing",
    props: {
        editStationData: {
            type: Array
        }
    },
    components: {
        MapComponent,

    },
    data() {
        return {
            config: {
                isShowDrawButton: true,
                isShowSearchBox: true
            },
            meterServiceOptions: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('StationName'),
                            name: "Station Name:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ServiceType'),
                            name: "Service Type",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                required: true,
                                max: 250,
                            },
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                required: true,
                                max: 250,
                            },
                            config: {
                                options: [{ name: this.$t("Active"), value: 1001 }, { name: this.$t("Inactive"), value: 1002 }],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AssociatedZipCodes'),
                            name: "Associated Zip Codes:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                        },

                        {
                            astype: "NumericField",
                            label: this.$t('PressureValue'),
                            name: "Pressure Value:",
                            value: 0,
                            placeholder: "",
                            validationRules: {

                                required: true,
                            },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    group_name: "Account Settings",
                    group_display_name: this.$t("AccountSettings"),
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('AddressLine1'),
                            name: "Address Line 1:",
                            value: "",
                            placeholder: "",

                        },
                        {
                            astype: "TextField",
                            label: this.$t('AddressLine2'),
                            name: "Address Line 2:",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('COUNTRY'),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                required: true,
                                max: 250,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('StateProvince'),
                            name: "State/Province",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                required: false,
                            },

                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City:",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                required: false,
                            },
                            config: {
                                options: [],
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZIPCODE'),
                            name: "Zip Code:",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: {
                                required: false,
                            },
                        },

                    ],
                },
            ],
            latLngValues: '',
            isStationId: 0,
            isUpdateForm: false
        };
    },

    async created() {
        var vm = this;
        await vm.GetCountryDDL();
        await vm.GetMeterServices();
        if (vm.editStationData.length != 0) {
            vm.isUpdateForm = true;
            vm.latLngValues = ''
            vm.isStationId = this.editStationData.STATION_ID;
            this.FormSchema[0].Data[0].value = this.editStationData.STATION_NAME;
            this.FormSchema[0].Data[1].value = this.editStationData.SERVICE_TYPE;
            this.FormSchema[0].Data[2].value = this.editStationData.STATUS_ID;
            this.FormSchema[0].Data[3].value = this.editStationData.ASSOCIATED_ZIP_CODES;
            this.FormSchema[0].Data[4].value = this.editStationData.PRESSURE_VALUE;
            this.FormSchema[1].Data[0].value = this.editStationData.ADDRESS_1;
            this.FormSchema[1].Data[1].value = this.editStationData.ADDRESS_2;
            this.FormSchema[1].Data[2].value = this.editStationData.COUNTRY_ID;
            this.FormSchema[1].Data[3].value = this.editStationData.STATE;
            this.FormSchema[1].Data[4].value = this.editStationData.CITY;
            this.FormSchema[1].Data[5].value = this.editStationData.ZIPCODE;

            this.Latitude = this.editStationData.LATITUDE;
            this.Longitude = this.editStationData.LONGITUDE;
            let obj = {
                location_nw: this.editStationData.LONGITUDE.replace(",", '') + this.editStationData.LATITUDE.replace(",", ',')
            }
            vm.latLngValues = obj;
        }
        else {
            vm.isStationId = 0;
            vm.isUpdateForm = false;
        }

    },

    methods: {
        clearForm() {
            var vm = this;
            vm.FormSchema[0].Data[0].value = ''
            vm.FormSchema[0].Data[1].value = ''
            vm.FormSchema[0].Data[2].value = ''
            vm.FormSchema[0].Data[3].value = ''
            vm.FormSchema[0].Data[4].value = ''

            vm.FormSchema[1].Data[0].value = ''
            vm.FormSchema[1].Data[1].value = ''
            vm.FormSchema[1].Data[2].value = ''
            vm.FormSchema[1].Data[3].value = ''
            vm.FormSchema[1].Data[4].value = ''
            vm.FormSchema[1].Data[5].value = ''
            this.$parent.editStationData = [];
            this.$parent.isCreateStation = false
        },
        GetMeterServices() {
            DataService.GetMeterServices().then(response => {
                this.meterServiceOptions = response.data
                this.meterServiceOptions.forEach((item) => {
                    this.FormSchema[0].Data[1].config.options.push({
                        name: `${item.key}`,
                        value: `${item.value}`,
                    });
                });
            })
        },
        GetCountryDDL() {
            var url = `moduleName=CRM&type=COUNTRIES`;
            var vm = this;
            DataService.GetDDLData(url).then((result) => {
                vm.countryList = result.data.DATA;
                vm.countryList.forEach((item) => {
                    vm.FormSchema[1].Data[2].config.options.push({
                        name: `${item.name}`,
                        value: `${item.value}`,
                    });
                });
                this.$refs.refAddress.UpdateKeyValue();
            });
        },

        GetAddress(data) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            // this.$refs.mapComponnet.someMethod();
            vueObj.$refs.mapComponnet.fullAddress = [];
            vueObj.$refs.mapComponnet.lngLatData = [];
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.countryList.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            vueObj.FormSchema[1].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            vueObj.FormSchema[1].Data[2].value = filterCountry;
            vueObj.FormSchema[1].Data[3].value = vueObj.FormSchema[1].Data[4].value = data[indexValue].CompleteAddress.State.LongName;
            vueObj.FormSchema[1].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            vueObj.FormSchema[1].Data[5].value = data[indexValue].CompleteAddress.PinCode;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }
                else {
                    dataLng1 = item.Lat;
                    dataLng2 = item.Lng;
                }
            });
            vueObj.Latitude = dataLng1 + ',' + dataLat2;
            vueObj.Longitude = dataLat1 + ',' + dataLng2;
        },
        onSubmit() {
            var vm = this;
            vm.$refs.refAddress.validateForm.validate().then(async valid => {
                if (valid) {
                    vm.isLoading = true;
                    let obj = {
                        stationId: vm.isStationId,
                        station_name: vm.FormSchema[0].Data[0].value,
                        service_type_id: parseInt(vm.FormSchema[0].Data[1].value),
                        status_id: vm.FormSchema[0].Data[2].value,
                        associated_zip_code: vm.FormSchema[0].Data[3].value,
                        pressure_value: vm.FormSchema[0].Data[4].value,

                        address_1: vm.FormSchema[1].Data[0].value,
                        address_2: vm.FormSchema[1].Data[1].value,
                        country_id: parseInt(vm.FormSchema[1].Data[2].value),

                        state: vm.FormSchema[1].Data[3].value,
                        city: vm.FormSchema[1].Data[4].value,
                        zipcode: vm.FormSchema[1].Data[5].value,

                        latitude: vm.Latitude,
                        longitude: vm.Longitude
                    }
                    console.warn(obj);

                    var formData = new FormData();
                    formData.append("postString", JSON.stringify(obj));
                    DataService.AddUpdateStation(formData).then(response => {
                        vm.isLoading = false;
                        if (response.data != null) {
                            if (response.data == 1) {
                                vm.ShowAlert(vm.$t("StationAddedSuccess"), "success", vm.$t('Alert'));
                                vm.clearForm();
                                let obj = {
                                    location_nw: "0,0"
                                }
                                vm.latLngValues = obj;
                                vm.FormSchema[0].Data[0].validationRules.required = false;
                                vm.FormSchema[0].Data[2].validationRules.required = false
                                vm.$refs.mapComponnet.clearSearchField();
                                vm.$refs.mapComponnet.resetMap();
                            }
                            else if (response.data == 2) {
                                vm.ShowAlert(vm.$t("BillingStationUpdated"), "success", vm.$t('Alert'));

                                vm.clearForm();
                                let obj = {
                                    location_nw: "0,0"
                                }
                                vm.latLngValues = obj;
                                vm.FormSchema[0].Data[0].validationRules.required = false;
                                vm.FormSchema[0].Data[2].validationRules.required = false
                                vm.isUpdateForm = false;
                                vm.$refs.mapComponnet.clearSearchField();
                                vm.$refs.mapComponnet.resetMap();
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t("Something went Wrong."), "success", vm.$t('Alert'));
                        }
                        this.$parent.FetchData();
                        this.$parent.editStationData = [];
                        this.$parent.isCreateStation = false

                    });


                }

            }).catch(error => {
                console.error('Error validating form:', error);
            });
        },

    }

}
</script>