<template>
    <section>
        <!-- test <loader :is-visible="isLoading"></loader> -->
        <tg-list :IsShowAction="true" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            SubModuleCode="Billing" IdentityColumn="STATION_ID" :HeaderText="$t('station')" :ListData="stationList"
            :HeaderData="headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp"
            :SortBy="sortBy" :RenderRowActionMethod="renderActions" :NorecordfoundText="$t('NoRecordfound')"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATION_NAME'">
                    <span class="text-dark" v-if="data.row.STATION_NAME">
                        <a href="javascript:;" v-on:click="clickEventOfColumn1(data.row)" class="relatedproduct"
                            :id="data.row.STATION_ID" :title="data.row.STATION_NAME"><em class="text-primary"
                                aria-hidden="true">{{
                                    data.row.STATION_NAME }}</em></a>
                    </span>

                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <td class="text-center single-action">
                        <div class="cstm-drop-btn">
                            <select v-model="data.row.STATUS_ID"
                                @change="DrpdownUpdataStatus(data.row.STATUS_ID, data.row.STATION_ID);"
                                :class="data.row.STATUS_ID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                <option value="1001" selected="selected">{{ $t("Active") }}</option>
                                <option value="1002">{{ $t("Inactive") }}</option>
                            </select>
                        </div>
                    </td>
                </template>

            </template>
        </tg-list>
        <AddNewStation v-if="isCreateStation" :editStationData=editStationData></AddNewStation>
        <SetGCV v-if="isGCVShow" :stationData="stationData"></SetGCV>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
import Schema from "../Billing/StationSchema";
import AddNewStation from "../../components/Modals/AddStation.vue";
import SetGCV from "../../components/Modals/SetGCV.vue";


export default {
    components: {
        AddNewStation,
        SetGCV
    },
    data() {
        return {
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            totalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            PageNumber: 1,
            widgets: [],
            headers: [{
                "COLUMN_NAME": 'STATION_NAME',
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('StationName'),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true,
                },
            },
            {
                "COLUMN_NAME": 'UTILITY_NAME',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ServiceType'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": 'ASSOCIATED_ZIP_CODES',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AssociatedZipCodes'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'STATUS_NAME',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Status'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true,
                },
            },
            {
                "COLUMN_NAME": 'COUNTRY_NAME',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('COUNTRY'),
                "DISPLAY_ORDER": 5,

            },
            {
                "COLUMN_NAME": 'STATE',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('STATE'),
                "DISPLAY_ORDER": 6,

            },

            {
                "COLUMN_NAME": 'CITY',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('City'),
                "DISPLAY_ORDER": 7,

            },
            {
                "COLUMN_NAME": 'ZIPCODE',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ZIPCODE'),
                "DISPLAY_ORDER": 8,

            }

            ],
            stationList: [],
            isLoading: true,
            listheaderbuttons: Schema.buttons,
            isCreateStation: false,
            editStationData: [],
            listActions: Schema.listActions,
            isGCVShow: false,
            stationData: []
        }
    },
    created() {
        this.FetchData();
    },
    methods:
    {
        pagerMethod(value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {

                case 'Delete':
                    vm.DeleteStation(item);
                    break;
                case 'Set GCV':
                    vm.SetUGCStation(item);
                    break;
            }
        },
        SetUGCStation(itm) {
            var vm = this;
            vm.isGCVShow = true
            vm.stationData = itm;
        },
        DeleteStation(itm) {
            var vm = this;
            vm.isLoading = true
            vm.confirmR(vm.$t('AreYouSureToUpdateStatus'), true, false, function (result) {
                let param = `station_id=${itm.STATION_ID}`
                DataService.DeleteStationData(param).then(response => {
                    if (response.data.status == 1) {
                        // vm.ShowAlert(vm.$t("Billing station successfully deleted."), "success", vm.$t('Alert'));
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('station')]), "success", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', "failure", vm.$t("Alert")));
                        // vm.ShowAlert(vm.$t("Something went Wrong."), "failure", vm.$t('Alert'));
                    }
                    vm.FetchData();
                })
            })

        },
        DrpdownUpdataStatus(StatusID, ID) {
            var vm = this;
            var StatusId = StatusID
            var Id = ID;
            vm.confirmR(vm.$t('AreYouSureToUpdateStatus'), true, false, function (result) {
                var url = `station_id=${Id}&statusId=${StatusId}`
                DataService.UpdateStationStatus(url).then(
                    response => {
                        if (response.data.status == 1) {
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully', "Success", true, vm.$t('Alert')));
                            vm.FetchData();
                        }
                    }
                )
            })

            vm.FetchData();

        },
        renderActions: function (row) {

            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Set GCV':
                        action.isVisible = true;
                        break;

                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: function () {

            var vm = this;
            vm.isLoading = true;
            DataService.GetStationList().then(response => {
                if (response.data.status == 'Success') {
                    console.log(response.data.result);
                    try {
                        let result = JSON.parse(response.data.result).data;
                        if (result.length > 0) {
                            vm.stationList = result;
                        } else {
                            vm.stationList = [];
                        }
                    } catch (error) {
                        vm.stationList = [];
                    }
                } else {
                    vm.stationList = [];
                }
                vm.isLoading = false;
            }).catch(error => {
                vm.stationList = [];
                vm.isLoading = false;
            });
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Add":
                    this.AddNew();
                    break;
            }
        },
        AddNew: function () {
            var vm = this;
            vm.isCreateStation = true;
        },
        clickEventOfColumn1: function (itm) {
            var vm = this;
            vm.isCreateStation = true;
            vm.editStationData = itm;
        },
    }
}
</script>